import { ApiService } from "../x-vue/services/api.service";

import config from "@/service/app.config";

import store from "../store";
import router from "@/router";
import { XHelper } from "@/x-vue-helper/x-helper";
import { Err } from "@/x-vue/services/defines";
import { Banner, Banners } from "@/x-vue/interfaces/advertisement.interface";
import {
  CafeSettings,
  MainCafeSettings,
  RequestCafeCreate,
  RequestData,
  ResponseData,
  UserModel,
} from "@/x-vue/interfaces/interfaces";
import { AdvertisementService } from "@/x-vue/services/advertisement.service";
import { FirebaseService } from "@/x-vue/services/firebase.service";
import ComponentService, { PLACEMENT } from "@/x-vue/services/component.service";

export class AppService {
  private static _instance: AppService;
  public api: ApiService = ApiService.instance;
  public x: XHelper = XHelper.instance;

  private constructor() {
    this.init();
  }

  /**
   * Initialize service
   * This will also initialize the current logged in user base on saved `sessionId` on the `localStorage`.
   *
   * @attention It get data from backend;
   *  - App settings,
   *  - Cafe settgins,
   *  - Country data,
   *  - Advertisement settings,
   *
   */
  async init(): Promise<void> {
    this.initCafe();
    this.api.countryAll();

    AdvertisementService.instance.init({
      open: (banner) => this.openBanner(banner),
    });
    AdvertisementService.instance.advertisementSettings();
    store.commit("banners", await AdvertisementService.instance.loadBanners(this.cafeDomain));
  }

  initFirebase(): void {
    FirebaseService.instance.init(
      {
        apiKey: "AIzaSyDWiVaWIIrAsEP-eHq6bFBY09HLyHHQW2U",
        authDomain: "sonub-version-2020.firebaseapp.com",
        databaseURL: "https://sonub-version-2020.firebaseio.com",
        projectId: "sonub-version-2020",
        storageBucket: "sonub-version-2020.appspot.com",
        messagingSenderId: "446424199137",
        appId: "1:446424199137:web:f421c562ba0a35ac89aca0",
        measurementId: "G-F86L9641ZQ",
      },
      {
        onMessage: (payload: Record<string, Record<string, unknown>>) => {
          const notification = payload.nostification;
          const data = payload.data ? payload.data : {};
          let url = "";

          // check if the sender is current loggedIn user
          if (store.state.user.idx == data["senderIdx"]) return;

          /**
           * If the type is post then move it to a specific post.
           */
          if (data["type"] === "post") {
            url = "/" + data["idx"];
          }

          /**
           * If the type is chat then move it to private chat.
           */
          if (data["type"] === "chat") {
            url = "/chat/" + data["idx"];
          }

          // show toast
          ComponentService.instance.toast({
            title: notification.title as string,
            message: notification.body as string,
            placement: PLACEMENT.BOTTOM_RIGHT,
            hideDelay: 10000,
            clickCallback: () => {
              if (store.state.router.currentRoute.path == url) {
                // return store.state.router.go(0);
                return store.commit("postViewKey");
              }
              store.state.router.push(url);
            },
          });
        },
      }
    );
    /**
     * Firebase user change
     */
    FirebaseService.instance.auth.onAuthStateChanged((user) => {
      console.log("firebase user email;", user?.email);
      console.log("firebase user displayName;", user?.displayName);
    });
  }

  initCafe(): void {
    // Cafe 1. set default global cafe settings for fast load.
    store.commit("cafeSettings", config.defaultCafeSettings);
    // Cafe 2. then, get global cafe settings from backend.
    this.loadGlobalCafeSettings(this.cafeDomain);
    // Cafe 3. get current cafe settings.
    this.loadCurrentCafeCategorySettings();
    // Cafe 4. load my afe lists.
    this.loadMyCafeList();
  }

  get bannerList(): Banners {
    let category = store.state.currentCategory;

    if (!store.state.banners[category]) {
      category = "global";
    }

    /// Check if global banner exists,
    if (!store.state.banners[category]) return {};
    return store.state.banners[category];
  }

  get mainCafeSettings(): MainCafeSettings {
    return store.state.cafeSettings.mainCafeSettings[this.api.rootDomain];
  }

  get cafeName(): string {
    return "Root domain nmame or subdomain name?";
  }

  get cafeDomain(): string {
    return location.hostname;
  }

  /**
   * Returns root cafe logo or sub cafe logo.
   *
   * ! @todo for the sub cafe, show domain name if it does not have image url.
   */
  get cafeLogo(): string {
    // if current cafe has logo, then display it.
    if (store.state.currentCafe.titleImageUrl) {
      return store.state.currentCafe.titleImageUrl;
    } else {
      // Or display root cafe's logo.
      return this.mainCafeSettings.logo;
    }
  }

  /**
   * Returns main cafe menus (forum category Ids)
   *
   * Display root cate menus. This will overwrite the menus in index.html
   */
  get mainCafeMenus(): string[] {
    return store.state.cafeSettings.mainMenus;
  }

  get subCafeMenus(): string[] {
    return store.state.currentCafe.subcategoriesArray;
  }

  public static get instance(): AppService {
    if (!this._instance) this._instance = new AppService();
    return this._instance;
  }
  /**
   * returns true if the user is logged in, false if not.
   * It will check if `this.user` is not null.
   *
   * @returns boolean
   */
  get loggedIn(): boolean {
    return store.state.user.loggedIn;
  }

  /**
   * return true if user is not logged in, false if logged in.
   *
   * @returns boolean
   */
  get notLoggedIn(): boolean {
    return !this.loggedIn;
  }

  get user(): UserModel {
    return store.state.user;
  }

  /**
   * handle and shows error.
   *
   * @param e error code
   *
   * @return true when the alert(error) modal has closed.
   */
  // eslint-disable-next-line
  async error(e: any): Promise<void> {
    // console.error("code: ", code, typeof code);
    if (typeof e === "string" && e.indexOf("error_") === 0) {
      return this.x.error(e);
    }
    if (e.code) {
      // firebase error and may be others.
      console.error(e);
      return this.alert("error", e.code);
    } else {
      return this.alert("error", e);
    }
  }

  async alert(title: string, content: string): Promise<void> {
    return ComponentService.instance.alert(title, content);
  }

  // toast(message: string) {
  //   this.x.toast(message);
  // }

  removeAllChildNodes(parent: HTMLElement | null): void {
    while (parent?.firstChild) {
      parent.removeChild(parent.firstChild);
    }
  }
  get userLanguage(): string {
    return this.api.userLanguage;
  }

  get rootDomain(): string {
    return this.api.rootDomain;
  }

  setCookie(k: string, v: string): void {
    return this.api.setCookie(k, v);
  }

  // portal(): void {
  //   $("[portal-to]").each(function () {
  //     const id = $(this).attr("portal-to");
  //     $("#" + id)
  //       .empty()
  //       .append($(this));
  //   });
  // }

  /**
   * Open sub cafe
   *
   * For live server, it is okay to move `https://domain`.
   * But for local development on developer's computer, it needs to add port.
   *
   * @param domain cafe domain
   */
  openCafe(domain: string): void {
    if (process.env.NODE_ENV === "development") {
      this.redirect("//" + domain + ":" + location.port);
    } else {
      this.redirect("//" + domain);
    }
  }

  /**
   * Redirect the page by moving(reloading) the page. The vue will lose its state and reboot.
   * @param url url
   */
  redirect(url: string): void {
    location.href = url;
  }

  open(url: string): void {
    // console.log("AppService::open::url", url);
    router.push(url);
  }

  async login(form: RequestData): Promise<void> {
    const user = await this.api.login(form);
    console.log("login user;", user);
    this.open("/");
    this.loadMyCafeList();
    // console.log("myCafe", store.state.myCafeList);
  }
  async register(form: RequestData): Promise<void> {
    await this.api.register(form);
    this.open("/");
  }
  /**
   * 로그아웃
   *
   * 파이어베이스에서도 같이 로그 아웃을 한다.
   */
  logout(): void {
    this.api.logout();
    FirebaseService.instance.auth.signOut();
    store.commit("myCafeList", []);
  }

  async loadMyCafeList(): Promise<void> {
    store.commit("myCafeList", await ApiService.instance.myCafeList());
  }

  /**
   * Load current cafe setting.
   *
   * If the current cafe is main cafe, then there is no settings. The app should use
   * global settings, then.
   *
   * @note you can call this to reload cafe settings. Especially on cafe admin page.
   */
  async loadCurrentCafeCategorySettings(): Promise<void> {
    const options: RequestData = {
      domain: this.cafeDomain,
    };

    try {
      store.commit("currentCafe", await ApiService.instance.cafeGet(options));
      // console.log("loadCafe", store.state.currentCafe);
      store.commit("isSubCafe");
    } catch (e) {
      if (e === Err.main_cafe_has_no_cafe_category_record) {
        store.commit("isRootCafe");
        // If the cafe category record does not exists,
        // then, it might be the root site of the cafe is not being created.
        // this.app.error(e);
        // console.log(e);
      } else if (e === Err.cafe_not_exists) {
        console.log(e);
        this.error("@todo set dev environment to move to local main cafe");
      } else {
        this.error(e);
      }
    }
  }

  async updateCurrentCafeCategorySettings(options: { [keys: string]: string | number }): Promise<void> {
    try {
      console.log("options; ", options);
      store.commit("currentCafe", await ApiService.instance.cafeUpdate(options));
      console.log("cafeUpdate; ", store.state.currentCafe);
      this.alert("Cafe Update", "Cafe update success");
    } catch (e) {
      this.error(e);
    }
  }

  // load global cafe settings for the root domain of this domain.
  async loadGlobalCafeSettings(domain: string): Promise<CafeSettings> {
    // Get cafe global settings

    // 캐시된 데이터가 있으면 그것을 먼저 사용
    const json = localStorage.getItem("cafeSettings");
    if (json) {
      store.commit("cafeSettings", JSON.parse(json) as CafeSettings);
    }
    // 서버로 부터 데이터를 가져와 캐시
    const res = await ApiService.instance.request("cafe.settings", {
      domain: domain,
    });
    store.commit("cafeSettings", res as CafeSettings);
    localStorage.setItem("cafeSettings", JSON.stringify(res));
    return store.state.cafeSettings;
  }

  async createCafe(form: RequestCafeCreate): Promise<void> {
    const cafe = await this.api.cafeCreate(form);
    this.openCafe(cafe.id);
    this.loadMyCafeList();
  }

  async refreshProfile(): Promise<void> {
    const user = await this.api.refreshLoginUserProfile();
    store.commit("user", user);
  }

  // async loadAdvertisementSettings(): Promise<ResponseData> {
  //   return await ApiService.instance.advertisementSettings();
  // }

  /**
   * Return true if the current cafe belongs to a country domain like philov.com
   */
  get isCountryDomain(): boolean {
    return store.state.cafeSettings.countryDomains.includes(this.rootDomain);
  }

  /**
   * Return country cafe settings if current domain is country domain like philov.com
   *
   * @note if it's not a country domain, then it returns the root - 'sonub.com' domain settings.
   */
  get countryDomainSettings(): MainCafeSettings {
    if (this.isCountryDomain) {
      return store.state.cafeSettings.mainCafeSettings[this.rootDomain];
    } else {
      return store.state.cafeSettings.mainCafeSettings["sonub.com"];
    }
  }

  get countryCode(): string {
    return this.countryDomainSettings.countryCode;
  }

  async advertisementCountryBannerPoints(): Promise<ResponseData> {
    const c = this.countryCode;
    const settings = await AdvertisementService.instance.advertisementSettings();
    return settings.point[c] ?? settings.point["default"];
  }

  // eslint-disable-next-line
  isEmptyObject(obj: any): boolean {
    return Object.keys(obj).length === 0 && obj.constructor === Object;
  }

  /**
   * Return url of sub cafe post list
   * @param subcategory subcategory
   * @returns url of sub cafe post list page
   */
  subCafePostListUrl(subcategory: string): string {
    return "/forum/" + this.cafeDomain + "?sc=" + subcategory;
  }

  openBanner(banner: Banner): void {
    // console.log("openBanner::banner", banner);
    if (banner.clickUrl) {
      window.open(banner.clickUrl, "_newtab");
    }
    if (banner.idx) {
      // const path = "/advertisement/view/" + this.currentBanner.idx;
      // Service.instance.open(path);
      this.open("/advertisement/view/" + banner.idx);
    }
  }
  // x는 숫자
  numberWithCommas(x: number): string {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
  }

  /**
   * 파이어베이스로 로그인
   *
   * 파이어베이스 소셜 로그인이 아닌 경우,
   *  - 예) 구글, 페이스북, 애플은 파이어베이스 로그인,
   *  - 예) 네이버, 카카오톡, 백엔드로 회원가입 등은 파이어베이스 소셜 로그인이 아님.
   * 파이어베이스로 로그인(또는 회원가입)을 하도록 한다.
   */
  async loginOrRegisterIntoFirebase(): Promise<void> {
    const auth = FirebaseService.instance.auth;
    try {
      // 먼저, 로그인 시도.
      await auth.signInWithEmailAndPassword(this.user.email, this.user.sessionId);
    } catch (e) {
      // 만약, 회원 가입이 안되어 있으면, 가입.
      if (e.code == "auth/user-not-found") {
        await auth.createUserWithEmailAndPassword(this.user.email, this.user.sessionId);
      } else {
        throw e;
      }
    }
  }
}
